let contactsError = getError("Ошибка: Укажите минимум 1 контакт");
let contactsInpError = getError("Ошибка: Заполните контакт");

// Сортировка
let sortCol = "fio";
let sortDir = true;

function getSort(arr, prop, dir) {
  return arr.sort((a, b) => {
    if (!dir === false ? a[prop] < b[prop] : a[prop] > b[prop]) return -1;
  });
}

