import { noClients } from "./helperFunc.js";

const SERVER_URL =
  "https://clients-maks0n.amvera.io" /* "http://localhost:3000" */;

// Функция добавления на сервер
export async function serverPost(clients) {
  const response = await fetch(SERVER_URL + "/api/clients", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(clients),
  });
  const data = await response.json();
  return data;
}

// Функция получения с сервера
export async function serverGet() {
  try {
    const response = await fetch(SERVER_URL + "/api/clients", {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    });
    const data = await response.json();
    return data;
  } catch {
    noClients("Ошибка сервера попробуете повторить запрос позже");
  } finally {
    preloader.classList.add("preloader_hide");
  }
}

// Функция получения с сервера id
export async function serverGetId(id) {
  const response = await fetch(SERVER_URL + "/api/clients/" + id, {
    method: "GET",
    headers: { "Content-Type": "application/json" },
  });
  const data = await response.json();
  return data;
}

// Функция изменения на сервер
export async function serverPatch(id, inpName, inpLastname, inpSurname, contacts) {
  const response = await fetch(SERVER_URL + "/api/clients/" + id, {
    method: "PATCH",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({
      name: inpName,
      surname: inpSurname,
      lastName: inpLastname,
      contacts: contacts,
    }),
  });
  const data = await response.json();
  return data;
}

// Функция удаления на сервер
export async function serverDelete(id) {
  const response = await fetch(SERVER_URL + "/api/clients/" + id, {
    method: "DELETE",
  });
  const data = await response.json();
  return data;
}
