// Функция создания одного элемента
export const createEl = (element, classList, id = "") => {
  const el = document.createElement(element);
  el.setAttribute("id", id);
  el.classList.add(classList);
  return el;
};

// Функция создания кнопки
export const createBtn = (classList, text) => {
  const button = document.createElement("button");
  button.classList.add(classList);
  button.innerHTML = text;
  button.type = "button";
  return button;
};

// Функция создания select

export function createSelect(optionsArray) {
  const select = document.createElement("select");
  select.classList.add("js-choice");

  for (let i = 0; i < optionsArray.length; i++) {
    let option = document.createElement("option");
    option.textContent = optionsArray[i];
    option.value = optionsArray[i];
    select.append(option);
  }
  return select;
}

// Функция создания input
export function createInput(placeholder = "", classList, type) {
  const input = document.createElement("input");
  input.classList.add(classList);
  input.placeholder = placeholder;
  input.type = type;
  return input;
}

